import './styles/App.scss';
import { Appbar } from './components';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation
} from "react-router-dom";
import routes from "./router";
import {
  TransitionGroup,
  CSSTransition
} from "react-transition-group";
import ProtectedRoute from './routes'

const AnimatedApp = () => {
  const location = useLocation();
  return (
    <TransitionGroup>
      <CSSTransition
        key={location.key}
        classNames="fade"
        timeout={750}
      >
        <Switch>
          {routes.map((route, index) => (
            route.protected ? <ProtectedRoute key={index} {...route} /> : <Route key={index} {...route} />
          ))}
        </Switch>
      </CSSTransition>
    </TransitionGroup>
  );
}

function App() {
  return (
    <div className="App">
      <Router>
        <Appbar />
        <AnimatedApp />
      </Router>
    </div>
  );
}

export default App;
