import {connect} from 'react-redux';
import {Col} from "react-bootstrap";
import {LoginForm} from "../components";
import { Link } from "react-router-dom";
import CQAPI from "../api";

// TODO: Extract to Child
export const Login = (props) => {
    const handleLogin = (value) => {
        props.onSuccess(value);
        props.history.push('/verify-phone?redirect=assessment-submit')
    }
    return (
        <div className="fullscreen flex-center flex-column">
            <Col sm={8} md={6} lg={4}>
                <LoginForm errors={props.errors} onSubmit={handleLogin} />
            </Col>
        </div>
    )
};

function mapStateToProps(state) {
    return {
        user: state.auth.user,
        errors: state.auth.error,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onSuccess: (payload) => dispatch({ type: 'auth/setUser', payload: payload  }),
        signIn: (credentials) => dispatch({ type: 'auth/signIn', payload: credentials }),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Login)