const Hero = (props) => {
    const [show, setShow] = useState(false);
    const [images, setImages] = useState([])

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const importAll = (r) => {
        return r.keys().map(r);
    }
    useEffect(() => {
        setImages(importAll(require.context('../assets/svgs/company-logos', false, /\.(png|jpe?g|svg)$/)))
    }, []);

    return (
        <div className="hero-wrapper">
            <Modal show={show} onHide={handleClose} centered>
                <Modal.Header closeButton></Modal.Header>
                <Modal.Body className="text-center">
                    <p> Have you taken the CQ Assessment before?</p>
                    <p> <Link className="btn btn-primary btn-outline" to='/login' >Yes</Link>
                        <br />
                        <br />
                        <Link className="btn btn-primary " to='/assessment' >No</Link></p>
                </Modal.Body>

            </Modal>
            <section className="hero">
                <Row>
                    <Col xl={5}>
                        <h1 className="font-weight-bold mb-4">Lightspeed Career Progress Starts Now</h1>
                        <p className="text-landing">Take the Career Quotient Assessment to understand yourself better, and find the right courses and opportunities to take your career to the next level.</p>
                        <div onClick={handleShow} className="btn btn-lg btn-primary font-weight-bold" to='/assessment' >Take CQ Assessment</div>
                    </Col>
                    <Col className="d-xl-none flex-center">
                        <img class="img-fluid" src="assets/svgs/hero-mobile.png" alt="" />
                    </Col>
                </Row>
            </section>
            {/* <div class="customers text-center justify-content-center">
                <img class="img-fluid" src="assets/svgs/head-line.png" alt="" />
            </div> */}
             <Container className="">
                <Row className="customers justify-content-around d-none d-md-flex">
                    {images.map((image, index) => (<Col xs={2}className="text-center my-2 d-flex">
                        <a href="#" className="m-auto"><img className="img-fluid m-auto" key={index} style={{ maxHeight: 50 + 'px' }} src={image.default} alt="" /></a>
                    </Col>))}
                </Row>
            </Container>

        </div>
    )
};

