import { createStore, applyMiddleware } from 'redux'
import { composeWithDevTools } from 'redux-devtools-extension'
import CQAPI from './api';
import { combineReducers } from 'redux'
// import { connectRouter } from 'connected-react-router'
import { createBrowserHistory } from 'history'
// import { routerMiddleware } from 'connected-react-router'
export const history = createBrowserHistory()

const initialAuthState = {
    signup:false,
    user:false,
    tokens: false,
    error:[],
}

const initialAssessmentState = {
    report: {},
    responses: null,
}

const authReducer = (state = initialAuthState, action) => {
    if (action.type === 'auth/setUser') {
        return Object.assign({}, state, {user:action.payload.user, tokens: action.payload.tokens});
    }
    if (action.type === 'auth/setError') {
        console.error(action.payload);
        return Object.assign({}, state, {error: [action.payload]});
    }
    if (action.type === 'auth/clearError') {
        return Object.assign({}, state, {error: []});
    }
    if (action.type === 'auth/removeUser') {
        return Object.assign({}, state, {user:false, tokens: false});
    }
    return state;
};
const assessmentReducer = (state = initialAssessmentState, action) => {
    if (action.type === 'assessment/completed') {
        return Object.assign({}, state.assessment, {responses: action.payload});
    }
    if (action.type === 'assessment/reportResponse') {
        return Object.assign({}, state, {report: action.payload});
    }
    return state;
};

const fetchTodosMiddleware = store => next => action => {
    if (action.type === 'auth/findUser') {
      // Make an API call to fetch user from the server
        CQAPI.findUser(action.payload.data).then((res) => {
            store.dispatch({ type: 'auth/setUser', payload: res })
            action.payload.history.push('/verify-phone')
        }).catch((err) => store.dispatch({ type: 'auth/setError', payload: err.message}));;
    }
    
    if (action.type === 'auth/signUp') {
        // sign up new user
          CQAPI.signUp(action.payload.user).then((res) => {
              store.dispatch({ type: 'auth/setUser', payload: res });
              action.payload.history.push('/verify-phone');
          }).catch((err) => store.dispatch({ type: 'auth/setError', payload: err.message}));
      }
    
    if (action.type === 'auth/signIn') {
      // login in user
        CQAPI.signIn(action.payload.credentials).then((res) => {
            store.dispatch({ type: 'auth/setUser', payload: res })
            store.dispatch({type: "assessment/submit"})
            action.payload.history.push('/report')
        }).catch((err) => store.dispatch({ type: 'auth/setError', payload: err.message}));
    }
    
    if (action.type === 'assessment/submit') {
        const state = store.getState();
        CQAPI.submitResponse(state.assessment.responses, state.auth.tokens.access.token)
            .then((resp) => store.dispatch({type: 'assessment/reportResponse', payload: resp}));
    }
    return next(action)
  }

const composedEnhancer = composeWithDevTools(applyMiddleware(fetchTodosMiddleware,))
// applyMiddleware(fetchTodosMiddleware)

const createRootReducer = () => combineReducers({
    auth: authReducer,
    assessment: assessmentReducer
})

const store = createStore(createRootReducer(), {},composedEnhancer);

export default store; 