const AssessmentHalfWay = ({ data, onContinue }) => {
  return (
    <Row className="flex-center text-center">
      <Col className="p-0 text-center">
        <img src={data.imageSrc} width={320} alt="Assessment Complete" />
        <div className="my-5 text-center">
          <p className="h4 mb-3 font-weight-bold w-50 m-auto text-center">{data.title}</p>
          <p className="mb-3">{data.text}</p>
        <ProgressBar now={data.percentComplete} />
        <br />
        <p className="font-weight-bold">{data.percentComplete}% complete</p>
          <Button variant="primary" onClick={onContinue}>Continue</Button>
        </div>
      </Col>
    </Row>
  )
};


const AssessmentCompleteHeader = (props) => (
  <div>
    <img src="../assets/breaks/goal-animate.svg" alt="Assessment Complete" />
    {/* <img className="my-5" src="/assets/svgs/assessment_complete.png" alt="Assessment Complete" /> */}
    <div className="my-5">
      <p className="h4 font-weight-bold">Congratulations!</p>
      <p>You’ve completed the CQ Assessment</p>
    </div>
  </div>
);

const AssessmentCompleteFooter = (props) => {
  return props.isSubmissionComplete ? (
    <div>
      <p className="font-weight-bold">Your Results Are Ready To View</p>
      <p>Please take a look at your analytics and your future career oppurtunities!</p>
      <Link className="btn btn-primary" to="/report">View Report</Link>
    </div>
  )
    : (
      <div>
        <p className="font-weight-bold">Result Is Being Calculated</p>
        <p className="text-muted">Please give us some time to fetch the results for you</p>
        <br />
        <div className="m-auto">
          <ProgressBar animated={true} now={90} min={0} max={100} />
        </div>
      </div>
    )
};


const AssessmentComplete = (props) => {
   
    if (props.report === 'error' ) {
      return (<Container className="expanded" fluid>
      <Row className="justify-content-center">
        <Col className="text-center" md={4}>
          <img className="my-5" src="/assets/svgs/assessment_complete.png" alt="Assessment Complete" />
          <p className="font-weight-bold">Your Results Are Ready To View</p>
          <p>Please take a look at your analytics and your future career oppurtunities!</p>
          <Link>Sign Up</Link>
        </Col>
      </Row>
    </Container>)
  } else {
    return (
      <div className="fullscreen flex-center">
          <Row className="justify-content-center">
              <Col className="text-center">
                  <AssessmentCompleteHeader />
                  <AssessmentCompleteFooter isSubmissionComplete={props.isSubmissionComplete} />
              </Col>
          </Row>
      </div>
    )
  }
};


const AssessmentIntroduction = ({ onStart }) => {
  const [slideIndex, setSlideIndex] = useState(0);
  return (
    <Carousel interval={null} indicators={false} activeIndex={slideIndex}>
      {intro.map((slide, index) => <Carousel.Item key={index}>
        <AssessmentIntroductionSlide {...slide} />
        <footer className="mt-4 text-center">
          {index === 2 ?
            <Button variant="primary" onClick={onStart}>Start</Button> :
            <Button variant="primary" onClick={() => setSlideIndex(index + 1)}>Next</Button>}
        </footer>
      </Carousel.Item>)}
    </Carousel>
  )
}

const AssessmentIntroductionSlide = ({ index, title, text, imageSrc, infoText, onStart, onNext }) => {
  return (
    <article className="text-center">
      <header></header>
      <section className="row justify-content-center align-items-center">
        <div className="col-md-5">
          <p className="h4 font-weight-bold my-4">CQ Assessment</p>
          <img width="250" height="250" src={imageSrc} alt="" />
          <p className="h5 font-weight-bold my-4">{title}</p>
          <p className="text-muted">{text}</p>
          {/* <p className="text-gold h6 font-weight-bold my-4">Ready to get started?</p> */}
          <div>
            {/* <a className="text-muted" href="">{infoText}</a> */}
          </div>
        </div>
      </section>
    </article>
  )
}


const appbar = (props) => {

  return (
    <nav className="navbar navbar-expand-lg">
      <Link to="/">
        <img className="brand" src="assets/CareerQuotient.png" alt="Career Quotient Logo" />
      </Link>
      <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav ml-auto">
          <Nav activeKey={props.location.pathname}>
            {
                navBarMenuItems.map((item, key) => <Nav.Link key={key}><Link to={item.path}>{item.title}</Link></Nav.Link>)
            }
            {props.user ? <Nav.Link><Link onClick={props.logOut} to={'/'}>Logout</Link></Nav.Link> : <Nav.Link><Link to={'/login'}>Login</Link></Nav.Link> }
            {/* {console.log(props)} */}
          </Nav>
        </ul>
      </div>
    </nav>
  )
}

function mapStateToProps(state) {
  return {user: state.auth.user}
}

const mapDispatchToProps = dispatch => {
  return {
    logOut: () => dispatch({ type: 'auth/removeUser' }),
  }
}


const _Appbar = withRouter(appbar);

const Appbar = connect(mapStateToProps,mapDispatchToProps)(_Appbar)

