import React, { useState } from 'react'
import { Alert, Form, Button } from 'react-bootstrap';
import { Link } from "react-router-dom";
import { useFormik } from 'formik'
import CQAPI from "../api";


export const LoginForm = ({ onSubmit, errors }) => {
    const formic = useFormik({
        initialValues: { username: '', },
        onSubmit: value => {
            CQAPI.findUser(value.username).then((resp) => onSubmit(resp.user))
                .catch();
        },
        validate: value => {
            let errors = {}
            if (!value.username) {
                errors.username = 'Required'
            }
        }
    })
    return (
        <Form className="text-center" onSubmit={formic.handleSubmit}>
            <p className="title text-center">Tell Us a Little About Yourself</p>
            {errors.length === 0 ? null : <Alert variant="danger">
                {errors[0]}
            </Alert>}
            {formic.errors.username ? <Alert variant="danger">
                {formic.errors.username}
            </Alert> : null}
            <Form.Group controlId="username">
                <Form.Control type="text"
                    value={formic.values.username}
                    onChange={formic.handleChange}
                    onBlur={formic.handleBlur}
                    placeholder="Enter your registered phone number"
                    required />
            </Form.Group>
            <div className="text-left">
                <p className="mb-1">Do not Have an Account</p>
                <Link to="/sign-up" style={{ color: "dodgerblue" }} >Sign In</Link>
            </div>
            <br />
            <br />
            <Button type={"submit"} variant="primary">Login</Button>
            <br />
            <br />
        </Form>
    )
};

export const SignUpForm = (props) => {
    const formic = useFormik({
        initialValues: {
            firstName: '',
            lastName: '',
            mobile: ''
        },
        onSubmit: (values, user) => {
            props.onSubmit(values)
        },
        validate: values => {
            let errors = {}
            if (!values.firstName) {
                errors.firstName = 'First name is required'
            } else if (/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.firstName)) {
                errors.firstName = 'Do not enter Special Character'
            }
            if (!values.lastName) {
                errors.lastName = 'Last name is required'
            } else if (/[ `!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~]/.test(values.lastName)) {
                errors.lastName = 'Do not enter Special Character'
            }
            if (!values.mobile) {
                errors.mobile = 'Mobile is required'
            } else if (!/[0-9]{10,10}/.test(values.mobile)) {
                errors.mobile = 'enter only number of 10 digit'
            }
            return errors
        }
    })
    return (
        <Form autoComplete="new-password" noValidate onSubmit={formic.handleSubmit}>
            <p className="title text-center">Tell Us a Little About Yourself</p>

            <Form.Group controlId="firstName">
                {formic.errors.firstName && formic.touched.firstName ? <Alert variant="danger">
                    {formic.errors.firstName}
                </Alert> : null}
                <Form.Control autoComplete="off" value={formic.values.firstName}
                    onChange={formic.handleChange} type="text" minLength={1} onBlur={formic.handleBlur}
                    placeholder="First Name" required />
                {/* <Form.Control.Feedback type="invalid">{errors.firstName}</Form.Control.Feedback> */}
            </Form.Group>
            <Form.Group controlId="lastName">
                {formic.errors.lastName && formic.touched.lastName ? <Alert variant="danger">
                    {formic.errors.lastName}
                </Alert> : null}
                <Form.Control autoComplete="on" value={formic.values.lastName} onBlur={formic.handleBlur}
                    minLength={1} onChange={formic.handleChange}
                    type="text" placeholder="Last Name" required />
                {/* <Form.Control.Feedback type="invalid">{errors.lastName}</Form.Control.Feedback> */}
            </Form.Group>
            <Form.Group controlId="mobile">
                {formic.errors.mobile && formic.touched.mobile ? <Alert variant="danger">
                    {formic.errors.mobile}
                </Alert> : null}
                <Form.Control autoComplete="new-password" value={formic.values.mobile} onBlur={formic.handleBlur}
                    onChange={formic.handleChange}
                    type="text" placeholder="Mobile Number" required />
                {/* <Form.Control.Feedback type="invalid">{errors.mobile}</Form.Control.Feedback> */}
            </Form.Group>
            <p><small className="text-muted">One Time Password will be sent to the above entered mobile number</small>
            </p>
            <Button type="submit" variant="primary">Get OTP</Button>
            <p></p>
            <p>Already have an account?</p>
            <Link to="/login">Login</Link>
        </Form>
    )
};

export const OTPForm = (props) => {
    const formic = useFormik({
        initialValues: {
            otp: '',
        },
        onSubmit: (values, user) => {
            // console.log(values)
            props.onSubmit(values.otp)
        },
        validate: values => {
            let errors = {}
            if (!values.otp) {
                errors.otp = "OTP required"
            }
            return errors
        }
    })
    return (
        <Form onSubmit={formic.handleSubmit}>
            <p>Enter OTP</p>
            <p className="text-muted"><small>Enter the OTP sent to your registered mobile number</small></p>
            <Form.Group controlId="otp">
                {formic.errors.otp && formic.touched.otp ? <Alert variant="danger">
                    {formic.errors.otp}
                </Alert> : null}
                <Form.Control value={formic.values.otp} onChange={formic.handleChange} onBlur={formic.handleBlur} type="text" placeholder="OTP"
                    required />
            </Form.Group>
            {/* <div><small>00:30</small></div>
            <Button disabled variant="link">Resend OTP</Button> */}
            <br />
            <br />
            <Button type='submit' variant="primary">{props.submitText}</Button>
        </Form>
    )
}